body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Beeline Sans';
  src: url('../public/font/BeelineSans-Regular.eot');
  src: local('Beeline Sans Regular'), local('BeelineSans-Regular'),
  url('../public/font/BeelineSans-Regular.eot?#iefix') format('embedded-opentype'),
  url('../public/font/BeelineSans-Regular.woff2') format('woff2'),
  url('../public/font/BeelineSans-Regular.woff') format('woff'),
  url('../public/font/BeelineSans-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Beeline Sans';
  src: url('../public/font/BeelineSans-Black.eot');
  src: local('Beeline Sans Black'), local('BeelineSans-Black'),
  url('../public/font/BeelineSans-Black.eot?#iefix') format('embedded-opentype'),
  url('../public/font/BeelineSans-Black.woff2') format('woff2'),
  url('../public/font/BeelineSans-Black.woff') format('woff'),
  url('../public/font/BeelineSans-Black.ttf') format('truetype');
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Beeline Sans';
  src: url('../public/font/BeelineSans-Bold.eot');
  src: local('Beeline Sans Bold'), local('BeelineSans-Bold'),
  url('../public/font/BeelineSans-Bold.eot?#iefix') format('embedded-opentype'),
  url('../public/font/BeelineSans-Bold.woff2') format('woff2'),
  url('../public/font/BeelineSans-Bold.woff') format('woff'),
  url('../public/font/BeelineSans-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Beeline Sans';
  src: url('../public/font/BeelineSans-Medium.eot');
  src: local('Beeline Sans Medium'), local('BeelineSans-Medium'),
  url('../public/font/BeelineSans-Medium.eot?#iefix') format('embedded-opentype'),
  url('../public/font/BeelineSans-Medium.woff2') format('woff2'),
  url('../public/font/BeelineSans-Medium.woff') format('woff'),
  url('../public/font/BeelineSans-Medium.ttf') format('truetype');
