.mainPageContainer{
  display: flex;
  position: relative;
  height: 100vh; /* фолбэк для браузеров, которые не поддерживают пользовательские свойства */
  //height: calc(var(--vh, 1vh) * 100);
  //padding-top: 40px;
  overflow: hidden;
  flex-direction: column;
  justify-content: space-between;
  .bg{
    background: url("../../img/background.svg");
    position: absolute;

    left: -143px;
    width: 600px;
    top: -103px;
    z-index: 1;
    height: 604px;
  }
  .mainText{
    z-index: 2;
    color: #000;
    font-family: Beeline Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 40px 24px 0 24px;
    display: flex;
    flex-direction: column;
    background: white;
    padding: 24px;
    gap: 24px;
    img{
      max-width: 120px;
    }
  }
  .imgDragon{
    display: flex;
    justify-content: center;
    position: relative;
    .img{
      position: absolute;
      background-image: url("../../img/dragon.png");
      width: 263px;
      background-repeat: no-repeat;
      height: 303px;


      margin: auto;
      top: -20; left: 0; bottom: 0; right: 0;
    }
    img{
      z-index: 2;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin: 0 auto;
    }
    a{z-index: 100;}
    button{
      margin-bottom: 52px;
      color: white;
      border:inherit;
      border-radius: 16px;
      background: #6331C7;
      z-index: 100;
      padding: 20px 64px;
      font-family: Beeline Sans;
      font-size: 18px;
      font-weight: 700;
      line-height: 20px;
      letter-spacing: 0;
      text-align: left;

    }
  }
}
form#searchform 2 {
  display: none;
}
