.InterruptionContainer{
  height: 100%;
  gap: 65px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  .wrap{
    padding: 16px;
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 24px;
    background: white;
      .text{
        color: #000;
        font-family: Beeline Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
      }
    .textNext{
      color: #000;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
    .job{
      border-radius: 16px;
      background: #6331C7;
      padding: 12px 32px;

      color: #FFF;
      font-family: Beeline Sans;
      font-size: 18px;
      border: inherit;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      width: max-content;
    }
    .next{
      min-width: 251px;
      padding: 12px 32px;
      border-radius: 16px;
      width: max-content;
      background: #FFC800;
      border: inherit;
      color: #000;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }
}
