.finalContainer{
  display: flex;
  z-index: 2;
  justify-content: space-between;
  align-items: flex-end;
  gap: 16px;
  padding-bottom: 100px;
  flex-direction: column;
  margin-top: 24px;
  .wrap{
    padding: 16px;
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 24px;
    background: white;
    .title{
      color: #000;
      font-family: Beeline Sans;
      font-size: 20px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }
    .text{
      color: #000;
      font-family: Beeline Sans;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
    .job{
      border-radius: 16px;
      background: #6331C7;
      padding: 12px 32px;

      color: #FFF;
      font-family: Beeline Sans;
      font-size: 18px;
      border: inherit;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      width: max-content;
    }
  }
  .form{
    .title{
      color: #000;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
    input{
      border: inherit;
      border-bottom: 1px solid #E7E7E7;
      width: 100%;
      outline:none;
      font-feature-settings: 'clig' off, 'liga' off;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 24px; /* 133.333% */
      letter-spacing: 0.5px;
    }
    .acceptedBody {
      display: flex;
      gap: 10px;
      .check {
        cursor: pointer;
        min-width: 14px;
        max-width: 14px;
        min-height: 14px;
        max-height: 14px;
        border-radius: 100px;
        border: 1px solid #FFC800;

        &.accepted {
          background: #FFC800;
        }
      }
      .text{
        color: var(--project-text-secondary-text, #757575);
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: Beeline Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px; /* 112.5% */
        letter-spacing: -0.078px;
      }
    }
    .next{
      padding: 12px 32px;
      border-radius: 16px;
      width: max-content;
      background: #FFC800;
      border: inherit;
      color: #000;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      min-width: 252px;
      line-height: normal;
      &.disableForm{
        background: #D0D0D0;
      }
    }
  }
}
