.resultBody{
  height: 100%;
  gap: 65px;
  display: flex;
  z-index: 2;
  flex-direction: column;
  justify-content: center;
  .bodyResult{
    color: #000;
    leading-trim: both;
    text-edge: cap;
    font-family: Beeline Sans;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    position: relative;
    padding: 16px;
    gap: 8px;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    background: white;
    img{
      max-width: 160px;
      align-items: center
    }
  }
  .next{
    width: 100%;
    border-radius: 16px;
    background: #000;
    padding: 20px 80px;

    color: #FFF;
    leading-trim: both;
    text-edge: cap;
    font-family: Beeline Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
}
