.mainGameplay{
  padding-top: 24px;
  gap: 65px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  .bodyText{
    position: relative;
    padding: 16px;
    border-radius: 16px;
    gap: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: white;

    color: #000;
    text-align: center;
    font-family: Beeline Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    &.bodyTextUp{
      opacity: 0.7;
    }
    img{
      max-width: 160px;
      pointer-events: none;
      align-items: center
    }
    &:before{
      content: url("../../img/vector.svg");
      position: absolute;
      top: -30px;
    }
    &:after{
      content: url("../../img/vector.svg");
      transform: rotate(180deg);
      position: absolute;
      bottom: -30px;
    }
  }
  .itHappens{
    z-index: 100;
    border-radius: 16px;
    background-color: #FFF;
    padding: 24px;
    text-align: center;

    color: #202020;
    leading-trim: both;
    text-edge: cap;
    font-family: Beeline Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    &.itHappensUp{
      color: #FFF;
      background-color: #6331C7;
      box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    }
  }
  .itNotHappens{
    z-index: 100;
    border-radius: 16px;
    background-color: #000;
    padding: 24px;
    text-align: center;

    color: #FFF;
    leading-trim: both;
    text-edge: cap;
    font-family: Beeline Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    &.itNotHappensUp{
      border-radius: 16px;
      background-color: #6331C7;
      color: white;
      box-shadow: 0px -4px 4px 0px rgba(0, 0, 0, 0.25);
    }
  }
  @media screen and (max-width: 320px){
    .itNotHappens, .itHappens{
      padding: 16px;
    }
  }
}
